// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import iView from 'iview'
import App from './App'
import router from './router'
import 'iview/dist/styles/iview.css'
import store from '../vuex/store'
import request from './utils/request'

Vue.prototype.$http = request



Vue.use(iView)

Vue.config.productionTip = false





router.beforeEach((to, from, next) => {
  console.info("进入前置路由导航")
  console.info("from:",from)
  console.info("to:",to)
  console.info("to:",to.path)
  if(to.path === "/")
  {
    console.info("next => /main")
    next("/main")
  }
  console.info("to.meta.requireAuth:",to.meta.requireAuth)
  console.info("store.state.isLoginState:",store.state.isLoginState)




  // 判断该路由是否需要登录权限
  if (to.meta.requireAuth) {

    // 通过vuex state获取当前的token是否存在
    // console.log(isEmptyObject(store.state.user))



    store.commit('isLogin',sessionStorage.getItem("isLogin"))
    console.info("store.state.isLoginState:",store.state.isLoginState)

    if(store.state.isLoginState == "ok") {
      next()
    }
    else {
      next({
        path: '/login',
        query: {redirect: to.fullPath}  // 将跳转的路由path作为参数，登录成功后跳转到该路由
      })
    }
  }
  else {
    next();
  }
})



/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})







