import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  //mode:"history",
  routes: [
    {
      name:"login"
      ,path:"/login"
      ,component: (resolve) => require(['@/components/login'], resolve)
    },
    {
      path: '/main',
      component: (resolve) => require(['@/components/myIframe'], resolve)
      ,meta: {
        // 添加该字段，表示进入这个路由是需要登录的
        requireAuth: true
      }
      ,children:[
        {
          //空子路由 这样默认显示这个
          name:"indexMain",
          path:""
          ,component:(resolve) => require(['@/components/body1'], resolve)
          ,meta: {
            // 添加该字段，表示进入这个路由是需要登录的
            requireAuth: true
          }
        }
        ,

        {
          name:"按钮",
          path:"myButton"
          ,component:(resolve) => require(['@/components/a/myButton.vue'], resolve)
          ,meta:{
            requireAuth:true
            ,isTab:true
          }
        }
        ,{
          name:"es6手册",
          path:"es6"
          ,component:(resolve) => require(['@/components/a/es6.vue'], resolve)
          ,meta:{
            requireAuth:true
            ,isTab:true
          }
        }
        ,{
          name:"表格Grid",
          path:"Grid"
          ,component:(resolve) => require(['@/components/a/myGrid.vue'], resolve)
          ,meta:{
            requireAuth:true
            ,isTab:true
          }
        }

        ,{
          name:"卡片",
          path:"Card"
          ,component:(resolve) => require(['@/components/a/myCard.vue'], resolve)
          ,meta:{
            requireAuth:true
            ,isTab:true
          }
        }
        ,{
          name:"表格",
          path:"myTable"
          ,component:(resolve) => require(['@/components/a/myTable.vue'], resolve)
          ,meta:{
            requireAuth:true
            ,isTab:true
          }
        }
        ,{
          name:"树列表",
          path:"myTree"
          ,component:(resolve) => require(['@/components/a/myTree'], resolve)
          ,meta:{
            requireAuth:true
            ,isTab:true
          }
        }
        ,{
          name:"tabs标签",
          path:"myTabs"
          ,component:(resolve) => require(['@/components/a/myTabs'], resolve)
          ,meta:{
            requireAuth:true
            ,isTab:true
          }
        }
        ,{
          name:"json服务",
          path:"jsonServer"
          ,component:(resolve) => require(['@/components/a/jsonServer'], resolve)
          ,meta:{
            requireAuth:true
            ,isTab:true
          }
        }


        ]
    }
  ]
})



